import { useState } from "react"
import styles from "./About.module.css"
import { MotionValue, useTransform, motion, easeInOut } from "framer-motion"

type prop = {
    scrollY: MotionValue<number>
}

export default function About({ scrollY }: prop) {
    const transform1 = useTransform(scrollY, [0,746, 1492], ["translateY(600px)","translateY(0px)", "translateY(-600px)"],)
    const transform2 = useTransform(scrollY, [0,746, 1492], ["translate(-50%,800px)","translate(-50%,0px)", "translate(-50%,-1000px)"])

    return (
        <div className="container mx-auto px-20 py-20 w-full h-full flex flex-col justify-center items-start relative">
            <div className="w-fit">
                <h1 className="">關於我</h1>
                <p>桃園好男人</p>
                <p>畢業於
                    <strong>
                        臺灣大學
                    </strong>
                    數學系</p>
                <p>擁有一年以上React前端開發 實務經驗</p>
            </div>
            <div className="absolute w-[45rem] h-[40rem] opacity-20 bottom-6 right-0 group" >
                <motion.img src="./words/self.png" alt="self" className="w-full h-full object-cover grayscale " style={{ transform: transform1 }} />
                <motion.div className="absolute w-[25rem] h-28 bg-black  top-[42%] left-[50%] translate-x-[-50%]"style={{ transform: transform2 }}  ></motion.div>
            </div>
            {/* <button onClick={()=>{setActive(pre=>!pre)}}>active </button> */}
        </div>
    )
}