import styles from "./Head.module.css"
import { useScroll, useTransform, motion, easeInOut } from "framer-motion"
import { MotionValue } from "framer-motion";
import { useEffect, useRef } from "react";
//901 / 160

type prop = {
    scrollY: MotionValue<number>
    id: string
}
export default function Head({ scrollY, id }: prop) {
    const ref = useRef<HTMLDivElement>(null)
    const height = id == "head" ? window.innerWidth * 160 / 901 * 1.8 : 24
    // const head :HTMLDivElement|null = document.querySelector(`#${id}`)
    // console.log(head)
    // const height = ref.current? ref.current.offsetHeight*1.5 :300
    const duration = 300
    const start = window.innerHeight * 0.3 - 108
    const diff = window.innerHeight / 15

    const range: number[][] = [0, 0, 0, 0].map((_, index) => {
        const left = start + diff * index
        return [left, left + duration]
    })
    const transform = [
        useTransform(scrollY, range[0], ["translateY(0)", `translateY(-${height}px)`], { ease: easeInOut }),
        useTransform(scrollY, range[1], ["translateY(0)", `translateY(-${height}px)`], { ease: easeInOut }),
        useTransform(scrollY, range[2], ["translateY(0)", `translateY(-${height}px)`], { ease: easeInOut }),
        useTransform(scrollY, range[3], ["translateY(0)", `translateY(-${height}px)`], { ease: easeInOut })
    ]

    const init = {
        transform: "translateY(100%)"
    }
    const appear = {
        transform: "translateY(0%)"
    }
    // const transform = useTransform(scrollY, [280, 400], ["translateY(0)", `translateY(${height}px)`], { ease: easeInOut })
    return (
        <div className={styles.main} id={id} >
            <motion.img src="/words/J_.png" alt="J" style={{ transform: transform.at(3) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, delay: 0.4, ease: easeInOut }} />
            <motion.img src="/words/A_.png" alt="A" style={{ transform: transform.at(2) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, delay: 0.25, ease: easeInOut }} />
            <motion.img src="/words/S_.png" alt="S" style={{ transform: transform.at(1) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, delay: 0.1, ease: easeInOut }} />
            <motion.img src="/words/O_.png" alt="O" style={{ transform: transform.at(0) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, ease: easeInOut }} />
            <motion.img src="/words/N_.png" alt="N" style={{ transform: transform.at(0) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, ease: easeInOut }} />
            <motion.img src="/words/L_.png" alt="L" style={{ transform: transform.at(1) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, delay: 0.1, ease: easeInOut }} />
            <motion.img src="/words/I_.png" alt="I" style={{ transform: transform.at(2) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, delay: 0.25, ease: easeInOut }} />
            <motion.img src="/words/N_.png" alt="N" style={{ transform: transform.at(3) }}
                initial={init}
                animate={appear}
                transition={{ duration: 0.7, delay: 0.4, ease: easeInOut }} />
            <motion.div className="absolute w-full h-full top-[87%] bg-[#fafafa]" ></motion.div>

        </div>
    )
}