import * as React from "react";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { WorkType } from "../Works/Works";
import { CaretRightOutlined } from '@ant-design/icons';


const variants = {
    enter: (direction: number) => {
        return {
            x: direction > 0 ? 1000 : -1000,
            opacity: 0
        };
    },
    center: {
        zIndex: 1,
        x: 0,
        opacity: 1
    },
    exit: (direction: number) => {
        return {
            zIndex: 0,
            x: direction < 0 ? 1000 : -1000,
            opacity: 0
        };
    }
};

/**
 * Experimenting with distilling swipe offset and velocity into a single variable, so the
 * less distance a user has swiped, the more velocity they need to register as a swipe.
 * Should accomodate longer swipes and short flicks without having binary checks on
 * just distance thresholds and velocity > 0.
 */
const swipeConfidenceThreshold = 10000;
const swipePower = (offset: number, velocity: number) => {
    return Math.abs(offset) * velocity;
};

type prop = {
    work: WorkType,
    states: any
}
const delay = 0.3
const imgVariants = {
    "initial": {
        height: 128
    },
    "expand": {
        height: 300,
        width: 524,
        transition: {
            height: {
                delay
            }
        }
    }
}

const Carousel = ({ work, states }: prop) => {
    const [imageIndex, setImageINdex] = useState(0)
    const onClick = () => {
        if (imageIndex === work.imgs.length - 1) {
            setImageINdex(0);
            return
        }
        setImageINdex(pre => pre + 1)
    }
    React.useEffect(() => {
        if (states.selected !== work.position) {
            setImageINdex(0)
        }
    }, [states.selected])
    return (
        <>
            <div className=" relative">
                <motion.img initial="initail" animate={work.position === states.selected ? "expand" : "initial"} variants={imgVariants} src={work.imgs[imageIndex]} alt={work.name} className="z-10 bg-gray-300 w-full  object-contain mb-4 grayscale group-hover:grayscale-0" />
                {
                    work.position === states.selected ?
                        <CaretRightOutlined onClick={onClick} className="absolute right-0 text-[1.5rem] text-inherit hover:scale-105" />
                        : null
                }
            </div>
        </>
    );
};
export default Carousel;