const cards = [blog, chatroom, issue_manager, shop]

type prop = {
    scrollY: MotionValue<number>
}
export default function Works({ scrollY }: prop) {
    const [states, setStates] = useState({
        selected: "",
        "tl": "",
        "tr": "",
        "bl": "",
        "br": "",
    })
    useEffect(() => {
        return scrollY.on("change", () => {
            closeAll()
        })
    }, [])

    const OnClick = (position: Position, e: any) => {
        e.stopPropagation()
        if (position === "tl") {
            setStates({
                selected: position,
                "tl": "expandTL",
                "tr": "right",
                "bl": "down",
                "br": "down"
            })
        }
        else if (position === "tr") {
            setStates({
                selected: position,
                "tl": "left",
                "tr": "expandTR",
                "bl": "down",
                "br": "down"
            })
        }
        else if (position === "bl") {
            setStates({
                selected: position,
                "tl": "up",
                "tr": "up",
                "bl": "expandBL",
                "br": "right"
            })
        }
        else {
            setStates({
                selected: position,
                "tl": "up",
                "tr": "up",
                "bl": "left",
                "br": "expandBR"
            })
        }
    }
    const closeAll = () => {
        console.log("close cards")
        setStates({
            selected: "",
            "tl": "",
            "tr": "",
            "bl": "",
            "br": "",
        })
    }

    return (
        <div className="container mx-auto px-20 pr-8 py-20 w-full h-full flex justify-between items-center relative" onClick={() => closeAll()}>
            <div>
                <h1>開發作品</h1>
                <p>為您精選四個過去的開發專案</p>
                <p>點擊以查看更詳細內容</p>
            </div>
            <div className=" grid grid-cols-2" style={{ width: container.width, height: container.height, columnGap: gap.y, rowGap: gap.x }}>
                {cards.map((work, index) => (
                    <motion.div animate={states[work.position]} variants={cardVariants} transition={{ type: "tween" }} className={`border border-[#222222] px-3 py-4 group relative hover:bg-white flex flex-col justify-between ${work.position===states.selected?"hover:cursor-auto":"hover:cursor-pointer"}`} style={{ width: card.width, height: card.height }} onClick={(e) => { OnClick(work.position, e) }}>
                        <div>
                            <Carousel work={work} states={states} />
                            {/* <motion.img initial="initail" animate={work.position === states.selected ? "expand" : "initial"} variants={imgVariants} src={work.imgs[0]} alt={work.name} className="z-10 bg-gray-300  object-cover mb-4 grayscale group-hover:grayscale-0" /> */}
                            {work.text.map((text) => (
                                <div className=" font-medium text-[17px]">{text}</div>
                            ))}
                            {
                                work.position == states.selected ?
                                    work.content.map((line, index) => (
                                        <motion.p initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} exit={{ opacity: 0, y: 20 }} transition={{ delay: 0.5 + index * 0.1 }} className="mt-6 text-base font-medium">
                                            {line}
                                        </motion.p>
                                    ))
                                    : null
                            }
                        </div>
                        <div className="line-clamp-1 w-full text-sm text-gray-400">
                            {work.tags.map((tag) => (
                                <>#{tag} </>
                            ))}
                        </div>
                        {
                            work.position === states.selected && work.github !== "" ?
                                <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} exit={{ opacity: 0, y: 20 }} transition={{ delay: 0.7 }} className="absolute right-3 bottom-4 flex gap-2 text-gray-500 items-end">
                                    <span className="text-md font-semibold ">
                                        see more
                                    </span>
                                    <a href={work.github}> 
                                        <GithubFilled className="text-3xl hover:text-gray-400" />
                                    </a>
                                </motion.div>
                                :null
                        }
                    </motion.div>
                ))}
            </div>
        </div>
    )
}

const container = {
    height: window.innerHeight * 0.8,
    width: 550
};
const gap = {
    x: 30,
    y: 24
}
const card = {
    height: (container.height - gap.x) / 2,
    width: (container.width - gap.y) / 2
}
const grow = {
    up: (card.height + gap.x) * -1,
    down: (card.height + gap.x),
    right: (card.width + gap.y),
    left: (card.width + gap.y) * -1,
}
const delay = 0.3
const cardVariants = {
    stay: {
    },
    expandTR: {
        width: container.width,
        height: container.height,
        x: grow.left,
        transition: {
            type: "tween",
            height: {
                type: "tween",
                delay
            }
        }
    },
    expandTL: {
        width: container.width,
        height: container.height,
        transition: {
            type: "tween",
            height: {
                type: "tween",
                delay
            }
        }
    },
    expandBL: {
        width: container.width,
        height: container.height,
        y: grow.up,
        transition: {
            type: "tween",
            height: {
                type: "tween",
                delay
            },
            y: {
                type: "tween",
                delay
            }
        }
    },
    expandBR: {
        width: container.width,
        height: container.height,
        y: grow.up,
        x: grow.left,
        transition: {
            type: "tween",
            height: {
                type: "tween",
                delay
            },
            y: {
                type: "tween",
                delay
            }
        }
    },
    up: {
        y: grow.up,
        transition: {
            type: "tween",
            ease: "easeInOut",
            delay
        }
    },
    down: {
        y: 0,
        transition: {
            type: "tween",
            ease: "easeInOut",
            delay
        }
    },
    right: {
        x: grow.right,
        transition: {
            type: "tween"
        }
    },
    left: {
        x: grow.left,
        transition: {
            type: "tween"
        }
    }
}


const swipeConfidenceThreshold = 10000;
const swipePower = (offset: number, velocity: number) => {
    return Math.abs(offset) * velocity;
};


